import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './components/main/main.component';
import { PromotionsComponent } from './components/promotions/promotions.component';
import { BankingComponent } from './components/banking/banking.component';
import { AboutUsComponent } from './components/about-us/about-us.component';
import { FaqComponent } from './components/faq/faq.component';
import { JoinnowComponent } from './components/joinnow/joinnow.component';
import { GtermsComponent } from './components/gterms/gterms.component';


const routes: Routes = [
  {path:"",component:MainComponent},
  {path:"home",component:MainComponent},
  {path:"banking",component:BankingComponent},
  {path:"promotions", component:PromotionsComponent},
  {path:"aboutus", component:AboutUsComponent},
  {path:"faq",component:FaqComponent},
  {path:"fa",redirectTo:"faq",pathMatch:"full"},
  {path:"join",component:JoinnowComponent},
  {path:"terms", component:GtermsComponent},
  {path:"**",component:MainComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{anchorScrolling:"enabled",scrollPositionRestoration: 'enabled',scrollOffset: [0, 64]})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
